import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Components
import PageTitle from "./../layouts/PageTitle";

function Registration() {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const navigate = useNavigate();

  // Hm x l ng k
  const handleRegistration = async (e) => {
    e.preventDefault();

    // Kim tra mt khu v xc nhn mt khu c khp hay khng
    if (password !== confirmPassword) {
      toast.error("Mt khu v xc nhn mt khu khng khp", {
        position: "top-right",
        autoClose: 3000,
      });
      return;
    }

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_DOMAIN}/user/accounts/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({
            username: name,
            password: password,
            phone: phone,
            email: email,
            status: true,
            role: 2,
          }),
        }
      );

      if (!response.ok) {
        throw new Error("To ti khon khng thnh cng. Vui lng th li.");
      }

      const data = await response.json();

      // Hin th thng bo thnh cng
      toast.success(
        <>
          ng nhp thnh cng!
          <br />
          Bn s c chuyn sang trang ng nhp sau 5 giy...
        </>,
        {
          position: "top-right",
          autoClose: 5000,
        }
      );

      setTimeout(() => {
        navigate("/login"); // Chuyn n trang ch
      }, 5500);

      console.log("To ti khon thnh cng:", data);
    } catch (err) {
      // Hin th thng bo li
      toast.error(err.message, {
        position: "top-right",
        autoClose: 3000,
      });
    }
  };

  return (
    <>
      <div className="page-content">
        <PageTitle parentPage="Trang ch" childPage="To ti khon" />
        <section className="content-inner shop-account">
          <div className="container">
            <div className="row justify-content-center">
              <div className="col-lg-6 col-md-6 mb-4">
                <div className="login-area">
                  <form onSubmit={handleRegistration}>
                    <h4 className="text-secondary">To ti khon</h4>
                    <p className="font-weight-600">
                      Nu bn cha c ti khon, hy ng k ngay  tri nghim
                      nhng tin ch tuyt vi cng chng ti.
                    </p>
                    <div className="mb-4">
                      <label className="label-title">H v tn *</label>
                      <input
                        name="name"
                        required
                        className="form-control"
                        placeholder="Nhp h v tn"
                        type="text"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="label-title">S in thoi *</label>
                      <input
                        name="phone"
                        required
                        className="form-control"
                        placeholder="Nhp s in thoi"
                        type="tel"
                        value={phone}
                        onChange={(e) => setPhone(e.target.value)}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="label-title">a ch email *</label>
                      <input
                        name="email"
                        required
                        className="form-control"
                        placeholder="Nhp Email"
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="label-title">Mt khu *</label>
                      <input
                        name="password"
                        required
                        className="form-control"
                        placeholder="Nhp mt khu"
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                    </div>
                    <div className="mb-4">
                      <label className="label-title">Xc nhn mt khu *</label>
                      <input
                        name="confirmPassword"
                        required
                        className="form-control"
                        placeholder="Nhp li mt khu"
                        type="password"
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                      />
                    </div>
                    <div className="mb-5">
                      <small>
                        D liu c nhn ca bn s c s dng  ci thin
                        tri nghim ca bn trn trang web ny, qun l quyn
                        truy cp vo ti khon ca bn v cho cc mc ch khc
                        c m t trong chnh sch bo mt ca chng ti.
                        <br />
                        <Link to={"/privacy-policy"}>Chnh sch bo mt</Link>.
                      </small>
                    </div>
                    <div className="text-left">
                      <button
                        type="submit"
                        className="btn btn-primary btnhover w-100 me-2"
                      >
                        To
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <ToastContainer />
    </>
  );
}

export default Registration;
