import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Components
import PageTitle from "./../layouts/PageTitle";

function Login() {
  const [forgotPass, setForgotPass] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  // Hm x l ng nhp
  const handleLogin = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_DOMAIN}/user/login/`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({ email, password }),
        }
      );

      if (!response.ok) {
        throw new Error(
          "ng nhp khng thnh cng. Vui lng kim tra li email hoc mt khu."
        );
      }

      const data = await response.json();
      // X l ng nhp thnh cng (lu token, iu hng, vv.)
      Cookies.set("refresh", data.refresh, { expires: 10 }); // Lu 7 ngy
      Cookies.set("access", data.access, { expires: 1 });
      Cookies.set("email", data.email, { expires: 1 });
      Cookies.set("user_id", data.user_id, { expires: 1 });

      console.log("ng nhp thnh cng:", data);
      toast.success(
        <>
          ng nhp thnh cng!
          <br />
          Bn s c chuyn sang trang ch sau 5 giy...
        </>,
        {
          position: "top-right",
          autoClose: 5000,
        }
      );
      // Sau khi ng nhp thnh cng, chuyn hng n trang ch sau 3 giy
      setTimeout(() => {
        navigate("/"); // Chuyn n trang ch
      }, 5500);
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <ToastContainer />
      <div className="page-content">
        <PageTitle parentPage="Trang ch" childPage="ng nhp" />
        <section className="content-inner shop-account">
          <div className="container">
            <div className="row">
              <div className="col-lg-6 col-md-6 mb-4">
                <div className="login-area">
                  <div className="tab-content">
                    <h4>KHCH HNG MI</h4>
                    <p>
                      To ti khon vi chng ti  tn hng quy trnh thanh
                      ton nhanh chng, lu tr nhiu a ch giao hng, xem v
                      theo di n hng trong ti khon, cng nhiu tin ch
                      khc.
                    </p>
                    <Link
                      to={"/signup"}
                      className="btn btn-primary btnhover m-r5 button-lg radius-no"
                    >
                      To ti khon
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 mb-4">
                <div className="login-area">
                  <div className="tab-content nav">
                    <form
                      onSubmit={handleLogin}
                      className={` col-12 ${forgotPass ? "d-none" : ""}`}
                    >
                      <h4 className="text-secondary">ng nhp</h4>
                      <p className="font-weight-600">
                        Vui lng ng nhp nu  c ti khon
                      </p>
                      <div className="mb-4">
                        <label className="label-title">a ch Email *</label>
                        <input
                          name="email"
                          required
                          className="form-control"
                          placeholder="Nhp Email"
                          type="email"
                          value={email}
                          onChange={(e) => setEmail(e.target.value)}
                        />
                      </div>
                      <div className="mb-4">
                        <label className="label-title">Mt khu *</label>
                        <input
                          name="password"
                          required
                          className="form-control"
                          placeholder="Nhp mt khu"
                          type="password"
                          value={password}
                          onChange={(e) => setPassword(e.target.value)}
                        />
                      </div>
                      {error && <div className="text-danger mb-3">{error}</div>}
                      <div className="text-left">
                        <button
                          type="submit"
                          className="btn btn-primary btnhover me-2"
                          disabled={isLoading}
                        >
                          {isLoading ? "ang x l..." : "ng nhp"}
                        </button>
                        <Link
                          to={"#"}
                          className="m-l5"
                          onClick={() => setForgotPass(!forgotPass)}
                        >
                          <i className="fas fa-unlock-alt"></i> Qun mt khu
                        </Link>
                      </div>
                    </form>
                    <form
                      onSubmit={(e) => e.preventDefault()}
                      className={` col-12 ${forgotPass ? "" : "d-none"}`}
                    >
                      <h4 className="text-secondary">FORGET PASSWORD ?</h4>
                      <p className="font-weight-600">
                        We will send you an email to reset your password.
                      </p>
                      <div className="mb-3">
                        <label className="label-title">E-MAIL *</label>
                        <input
                          name="dzName"
                          required
                          className="form-control"
                          placeholder="Your Email Id"
                          type="email"
                        />
                      </div>
                      <div className="text-left">
                        <Link
                          to={"#"}
                          className="btn btn-outline-secondary btnhover m-r10 active"
                          onClick={() => setForgotPass(!forgotPass)}
                        >
                          Back
                        </Link>
                        <button
                          type="submit"
                          className="btn btn-primary btnhover"
                        >
                          Submit
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}

export default Login;
