//Imgaes
import about1 from './../assets/images/about/about1.jpg';
import about2 from './../assets/images/about/about2.jpg';


const missionBlog = [
    {iconClass:'flaticon-open-book-1', title:'Nh sch tt nht',description: 'Bookstore cam kt mang n nhng u sch phong ph v tri nghim tuyt vi cho ngi yu sch.' },
    {iconClass:'flaticon-exclusive', title:'Ni bn hng tin cy', description: 'Chng ti t ho l ni khch hng c th tin tng  tm kim kin thc v gii tr t sch.' },
    {iconClass:'flaticon-store', title:'Nng cao tri nghim',description: 'Bookstore lun i mi v ci tin  mang li nhng tri nghim mua sm tt nht cho cng ng c gi.' },
];

function AboutUs(){
    return(
        <>
            <div className="page-content bg-white">
               <PageTitle  parentPage="Trang ch" childPage="V Chng Ti" />
               <section className="content-inner overlay-white-middle">
                    <div className="container">
                        <div className="row about-style1 align-items-center">
                            <div className="col-lg-6 m-b30">
                                <div className="row sp10 about-thumb">
                                    <div className="col-sm-6 aos-item" >
                                        <div className="split-box">
                                            <div>
                                                <img className="m-b30" src={about1} alt="" />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="split-box ">
                                            <div>
                                                <img className="m-b20 aos-item" src={about2} alt=""  />
                                            </div>
                                        </div>
                                        <div className="exp-bx aos-item" >
                                            <div className="exp-head">
                                                <div className="counter-num">
                                                    <h2><span className="counter"> <CountUp end={69} /></span><small>+</small></h2>
                                                </div>
                                                <h6 className="title">Years of Experience</h6>
                                            </div>
                                            <div className="exp-info">
                                                <ul className="list-check primary">
                                                    <li>Sch - Truyn</li>
                                                    <li>Th - Tiu Thuyt</li>
                                                    <li>S Tay - Cng Thc</li>
                                                    <li>T in</li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-6 m-b30 aos-item">
                                <div className="about-content px-lg-4">
                                    <div className="section-head style-1">
                                        <h3 className="title">Bookstore - La chn hng u cho ngi yu sch</h3>
                                        <p><strong>Bookstore</strong> mang n cho bn v vn s la chn t nhng cun sch ph bin nht n cc tc phm chuyn su cho ngi hc. Vi mt kho tng tri thc phong ph, chng ti cam kt cung cp tri nghim c sch tuyt vi, gip bn tm thy nhng cun sch ph hp v ng tin cy nht. Hy  <strong>Bookstore </strong> ng hnh cng bn trn hnh trnh tri thc!</p>
                                    </div>
                                    <Link to={"/contact-us"} className="btn btn-primary btnhover shadow-primary">Lin H</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="content-inner-1 bg-light">
                    <div className="container">
                        <div className="section-head text-center">
                            <h2 className="title">S mnh ca chng ti</h2>
                            <p>Chng ti tin rng mi cun sch u mang theo mt th gii mi, m rng tri thc v tm hn. S mnh ca chng ti l to ra mt khng gian ni mi ngi c th tm thy ngun cm hng, tri thc, v nhng cu chuyn  nui dng c m. Chng ti cam kt mang n cc u sch phong ph, dch v tn tm v khng ngng ci tin  phc v c gi ngy cng tt hn.</p>
                        </div>
                        <div className="row">
                            {missionBlog.map((data, i)=>(
                                <div className="col-lg-4 col-md-6" key={i}>
                                    <div className="icon-bx-wraper style-3 m-b30">
                                        <div className="icon-lg m-b20">
                                            <i className={`icon-cell ${data.iconClass}`}></i>
                                        </div>
                                        <div className="icon-content">
                                            <h4 className="title">{data.title}</h4>
                                            <p>{data.description}</p>
                                            <Link to={"/about-us"}>Tm hiu thm <i className="fa-solid fa-angles-right"></i></Link>
                                        </div>
                                    </div>
                                </div>
                            ))}
                            </div>
                        </div>
                    </section>
                </div>
            </>
        )
    }
export default AboutUs;
